






























import { Component, Prop, Vue } from 'vue-property-decorator'
import './notification.less'

@Component
export default class ItNotification extends Vue {
  get typeIcon() {
    return {
      primary: 'info_outline',
      success: 'done',
      warning: 'error_outline',
      danger: 'clear'
    }
  }
  public id: number
  public show: boolean = false
  public text: string = ''
  public title: string
  public icon: string = ''
  public emoji: string = ''
  public duration: number = 5000
  public onClose: () => void
  public position = {}
  public placement: string
  public type: string = 'primary'
  public image: string = ''
  public timer: number | any

  public destroy() {
    this.$destroy()
    this.$el.parentNode!.removeChild(this.$el)
  }

  public mounted() {
    this.startTimer()
  }

  public startTimer() {
    if (this.duration > 0) {
      this.timer = setTimeout(() => {
        this.show = false
        if (this.onClose && typeof this.onClose === 'function') {
          this.onClose()
        }
      }, this.duration)
    }
  }

  public clearTimer() {
    clearTimeout(this.timer)
  }

  get backgroundImage() {
    return this.image ? `url(${this.image})` : 'none'
  }

  get typeColor() {
    if (this.emoji) {
      return '#fbfbfb'
    }
    return {
      primary: '#3051ff',
      success: '#07d85b',
      danger: '#F93155',
      warning: '#ffba00'
    }
  }

  get positionPx() {
    const posPx = {}
    for (const key in this.position) {
      if (this.position.hasOwnProperty(key)) {
        posPx[key] = this.position[key] + 'px'
      }
    }
    return posPx
  }
}
