



















import { Component, Prop, Vue } from 'vue-property-decorator'

@Component
export default class Col extends Vue {
  @Prop({ default: 12 }) public width?: number | string
  @Prop() public xs?: number | string
  @Prop() public sm?: number | string
  @Prop() public lg?: number | string
  @Prop({ default: 0 }) public offset?: number | string
  @Prop() public justify?: string
  @Prop() public align?: string
  @Prop() public order?: string | number
}
